import * as types from '../mutation-types';
import { get } from 'idb-keyval'

// initial state
const state = {
    all: [],
    favoritesByCategory: [],
    categorySingle: false,
};

// getters
const getters = {
    allFavorites: state => state.all,
    getFavoritesBySlug: (state) => (slug) => {
        return state.all.filter(fav => fav.slug === slug)
    },
    isOnlyOne: (state) => (slug) => {
        return (state.all.filter(fav => fav.slug === slug).length === 1)
    },
    isEmpty: (state) => (slug) => {
        return (state.all.filter(fav => fav.slug === slug).length === 0)
    },
};

// actions
const actions = {
    async getFavorites({ commit }) {
        commit(types.FETCH_FAVORITES, get('favorites'))
    },
};

// mutations
const mutations = {
    [types.FETCH_FAVORITES](state, favorites ) {
       state.all = favorites
    },
};

export default {
    state,
    getters,
    actions,
    mutations,
};