<template>
  <div class="home">
    <headerHome />
    <div class="home-content">
      <div class="description">
        <p>Pick a theme, grab a bottle of wine along with a partner,
          and get ready for some fun thought-provoking questions.</p>
      </div>
      <div class="card-nav">
        <div class="card-item">
          <router-link to="/cards/date-night">
            <div class="icon">
              <img :src="images.dateNight" alt="Date Night">
            </div>
            <div class="title">
              <span class="heading-3">Date Night</span>
            </div>
          </router-link>
        </div>
        <div class="card-item">
          <router-link to="/cards/girls-night">
            <div class="icon">
              <img :src="images.girlsNight" alt="Girls Night">
            </div>
            <div class="title">
              <span class="heading-3">Girls Night</span>
            </div>
          </router-link>
          <h3></h3>
        </div>
        <div class="card-item">
          <router-link to="/cards/family-friendly">
            <div class="icon">
              <img :src="images.familyFriendly" alt="Family Friendly">
            </div>
            <div class="title">
              <span class="heading-3">Family-Friendly</span>
            </div>
          </router-link>
        </div>
      </div>
    </div>
    <footerHome
        :isIOS="isIOS"
        :isAndroid="isAndroid"
        :isStandalone="isStandalone"
    />
  </div>
</template>

<script>
  import HeaderHome from "./partials/HeaderHome"
  import FooterHome from "./partials/FooterHome"

  // Assets
  import logo from '../assets/logo.svg'
  import dateNight from '../assets/date-night-icon.svg'
  import girlsNight from '../assets/girls-night-icon.svg'
  import familyFriendly from '../assets/family-friendly-icon.svg'

  export default {
    data() {
     return {
       favSlug: 'date-night',
       images: {
         logo: logo,
         dateNight: dateNight,
         girlsNight: girlsNight,
         familyFriendly: familyFriendly,
       },
     }
    },
    props: ['isStandalone', 'isIOS', 'isAndroid'],
    components: {
      headerHome: HeaderHome,
      footerHome: FooterHome
    },
  }
</script>

<style lang="scss">
  @import '../scss/colors';
  @import '../scss/text';
  @import '../scss/variables';

  .home-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    padding: 20px 0;
    height: calc(100vh - 200px - #{$footerHeight});

    @supports (-webkit-touch-callout: none) {
      /* CSS specific to iOS devices */
      height: calc(100%) !important;
    }

    @media screen and (min-width: 375px) {
      height: calc(100vh - #{$headerHomeHeight} - #{$footerHeight});
    }

    .description {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      p {
        text-align: center;
        max-width: 280px;
        margin: 0;

        @media screen and (max-width: 374px) {
          margin: 0;
        }
      }
    }

    .card-nav {
      display: flex;
      justify-content: center;

      a {
        text-decoration: none;
      }

      .card-item {
        margin: 20px 0;

        .icon {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          width: 70px;
          height: 70px;
          border: 2px solid $black;
          border-radius: 50%;
          margin: 10px 20px;

          @media screen and (max-width: 374px) {
            margin: 10px;
          }

          img {
            width: 22px;
          }
        }
        .title {
          margin-top: 5px;
          span {
            color: $black;
          }
        }
      }
    }
  }
</style>