<template>
  <div class="cards favorites">
    <mainHeader />
    <div class="favorites-title">
      <img :src="images.heart" alt="Heart Image">
      <span class="heading-3">{{ $route.params.slug.replace(/-/g, ' ') }}</span>
    </div>
    <div class="favorites-container">
      <div class="content-container">
        <div class="card-container">
          <div class="card-wrapper">
            <div class="card-item"
                 v-for="(fav, index) in favoritesBySlug($route.params.slug)"
                 :key="fav.question"
                 :data-index="index"
                 :class="{'card-active': index === 0}"
              >
              <!-- Favorite Question Card -->
              <div class="question">
                <div class="question-container">
                  <span class="paragraph-big">{{ fav.question }}</span>
                  <div class="nextButton heading-4" @click="goToNext(index)">Next Question</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="no-favorites" v-if="favEmpty">
        <span class="heading-2">No saved questions were found for this category.</span>
      </div>
    </div>
    <mainFooter
        :prevSlide="prevSlide"
        :cardSlide="favSlide"
        :favSlug="favSlug"
        :isFavorite="true"
        :isStandalone="isStandalone"
        :isAndroid="isAndroid"
        :isIOS="isIOS"
    />
  </div>
</template>

<script>
import { get, entries } from 'idb-keyval'
import {directive } from 'vue-awesome-swiper'

// Components
import Header from "../partials/Header";
import Footer from "../partials/Footer";

// Assets
import background from '../../assets/background.svg'
import arrow from '../../assets/arrow-right-icon.svg'
import heart from '../../assets/heart-red.svg'

export default {
  data() {
    return {
      isActive: undefined,
      isFirstCard: true,
      prevSlide: null,
      nextSlide: null,
      favorites: [],
      favEmpty: false,
      favSlug: this.$route.params.slug,
      lastSlide: false,
      images: {
        background: background,
        arrow: arrow,
        heart: heart
      },
    }
  },
  props: ['isStandalone'],
  components: {
    mainHeader: Header,
    mainFooter: Footer,
  },
  directives: {
    swiper: directive
  },
  methods: {
    setFavorites() {
      get('favorites').then((val) => {
        if (val !== undefined) {
          new Promise((resolve) => {
            val.forEach((obj) => {
              // Check for duplicates
              this.favorites.push(obj)
            })
            resolve()
          })
              .then(() => {
                if (this.favorites.filter(fav => fav.slug === this.favSlug).length === 0
                    || this.favorites === undefined) {
                  this.favEmpty = true
                }
              })
              .catch((error) => {
                console.log(error)
              })
        } else {
          this.favEmpty = true
        }
      })

      entries().then((entries) => {
        if (entries.length === 0) {
          this.favEmpty = true;
        }
      })
    },
    favoritesBySlug(slug) {
       return this.favorites.filter(fav => fav.slug === slug)
    },
    goToNext(index) {
      const data = {
        cardCountTotal: parseInt(document.querySelector('.card-wrapper').lastElementChild.dataset.index),
        nextCardIndex: parseInt((index += 1)),
        currentCardElement: document.querySelector('.card-item[data-index="' + (index -= 1) + '"]')
      }

      if (data.nextCardIndex <= data.cardCountTotal ) {
        data.currentCardElement.classList.remove('card-active')
        document.querySelector('.card-item[data-index="' + data.nextCardIndex + '"]').classList.add('card-active')
      } else {
        data.currentCardElement.classList.remove('card-active')
        document.querySelector('.card-item[data-index="0"]').classList.add('card-active')
      }
    }
  },
  mounted() {
    this.setFavorites()
  }
}
</script>

<style lang="scss">
@import '../../scss/colors';
@import '../../scss/text';
@import '../../scss/variables';

.favorites {
  .no-favorites {
    position: fixed;
    top: 50%;
    width: 100%;
    z-index: 6;
    text-align: center;
    display: block;

    &.show {
      display: none !important;
    }

    span {
      display: block;
      padding: 0 40px;
    }
  }

  .favorites-title {
    position: relative;
    top: 140px;
    z-index: 6;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    img {
      margin-bottom: 5px;
    }
  }
  .card-container {
    position: relative;
    overflow: hidden;
    width: 100%;
    z-index: 2;
  }

  .card-item {
    position: fixed;
    width: 100% !important;
    height: 100vh !important;
    background: $white;
    opacity: 0;
    visibility: hidden;

    @for $i from 1 through 100 {
      &:nth-child(#{$i}) {
        z-index: #{100 - $i};
      }
    }

    &.card-active {
      opacity: 1 !important;
      visibility: visible !important;
      //display: block !important;

      .paragraph-big {
        opacity: 1 !important;
      }
    }

    .question-container {
      position: relative;
      display: flex;
      height: 100vh;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      opacity: 1 !important;

      @supports (-webkit-touch-callout: none) {
        /* CSS specific to iOS devices */
        top: -40px !important;
      }

      .paragraph-big {
        padding: 0 40px;
        display: inline-block;
        max-width: 450px;
        opacity: 0;
        transition: all .5s ease-in-out;
      }

      .nextButton {
        margin-top: 40px;
        background: $black;
        padding: 15px 30px;
        border-radius: 9000px;
        color: $white;
        cursor: pointer;
      }

    }

    .question {
      width: 100% !important;
      height: calc(100% - 200px);
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin-top: 100px;

      @supports (-webkit-touch-callout: none) {
        /* CSS specific to iOS devices */
      }
      @media only screen and (min-device-width: 375px) and (max-device-height: 812px) and (orientation : landscape) and (-webkit-device-pixel-ratio: 3){
        /* Styles */

      }


      .save {
        position: absolute;
        right: 40px;
        top: calc(#{$headerHeight} + 20px);
        display: flex;
        align-items: center;
        cursor: pointer;
        transition: .25s ease;

        //@supports (-webkit-touch-callout: none) {
        //  top: calc(#{$headerHeight} + 60px);
        //}

        &:hover,
        &.saved {
          svg {
            fill: $red !important;

            path {
              stroke: transparent !important;
            }
          }
        }

        span {
          display: inline-block;
          padding-right: 5px;
          font-family: nimbus-sans, sans-serif;
          font-weight: 700;
          font-style: normal;
          font-size: 13px;
        }

        svg {
          width: 20px;
          height: 20px;
          fill: transparent;
          transition: .25s ease;

          path {
            stroke: $black;
            stroke-width: 1px;
            stroke-linecap: round;
            transition: .25s ease;
          }
        }
      }

      h2 {
        padding: 0 20px;
      }
    }
  }
}
</style>