<template>
  <div id="app">
    <!--  View Content  -->
    <transition>
      <router-view
          :key="$route.path"
          :isAndroid="isAndroid"
          :isIOS="isIOS"
          :isBrowser="isBrowser"
          :isStandalone="isStandalone">
      </router-view>
    </transition>
    <div class="pwa-banner"
         v-if="isStandalone === false"
         :class="{slideIn: openBanner}"
         @click="closeBanner"
      >
      <div class="pwa-banner-container">
        <svg viewPort="0 0 12 12" version="1.1"
             xmlns="http://www.w3.org/2000/svg">
          <line x1="1" y1="11"
                x2="11" y2="1"
                stroke="black"
                stroke-width="2"/>
          <line x1="1" y1="1"
                x2="11" y2="11"
                stroke="black"
                stroke-width="2"/>
        </svg>
        <p v-html="bannerMessage"></p>
      </div>
    </div>
  </div>
</template>

<script>
  import message from './messages'
  import { mapGetters } from 'vuex';

  export default {
    name: 'App',
    data() {
      return {
        isAndroid: false,
        isIOS: false,
        isBrowser: false,
        isStandalone: false,
        isLoaded: false,
        bannerMessage: ''
      }
    },
    computed: {
      ...mapGetters(['openBanner'])
    },
    methods: {
      getDisplayMode() {
        const standalone = window.matchMedia('(display-mode: standalone)').matches;
         if (navigator.standalone || standalone) {
           this.isStandalone = true;
        } else {
           this.isStandalone = false;
         }
        window.addEventListener('appinstalled', () => {

        });
      },
      getMobileOS() {
        const ua = navigator.userAgent
        if (/android/i.test(ua)) {
          this.bannerMessage = message.android
          this.isAndroid = true
        }
        else if (/iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream) {
          this.bannerMessage = message.ios
          this.isIOS = true
        }
        else {
          this.bannerMessage = message.android
          this.isBrowser = true
        }
      },
      closeBanner() {
        this.$store.dispatch('setOpenBannerStatus', false)
      },
    },
    mounted() {
      this.getMobileOS()
      this.getDisplayMode()
    }
  }
</script>

