<template>
  <div class="page">
    <Header />
      <div class="page-content-container"
           v-html="pageContent.page.content.rendered"></div>
    <Footer :isIOS="isIOS"
            :isAndroid="isAndroid"
            :isStandalone="isStandalone"
    />
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'

  //Components
  import Header from '../partials/Header'
  import Footer from '../partials/FooterHome'

  export default {
    components: {
      Header: Header,
      Footer: Footer,
    },
    props: ['isStandalone'],
    computed: {
      ...mapGetters(['pageContent'])
    },
    mounted() {
      this.$store.dispatch('getPage', this.$route.params.id)
    }
  }
</script>

