import * as types from '../mutation-types';

// initial state
const state = {
    openBanner: false,
};

// getters
const getters = {
    openBanner: state => state.openBanner,
}

// actions
const actions = {
    setOpenBannerStatus({ commit }, status) {
        commit(types.OPEN_BANNER, status)
    }
};

// mutations
const mutations = {
    [types.OPEN_BANNER](state, val ) {
        state.openBanner = val
    },
};

export default {
    state,
    getters,
    actions,
    mutations,
};