<template>
  <div class="cards" v-if="allCardsLoaded">
    <mainHeader v-if="!isAd" />
    <div
        class="cards-container"
        v-for="card in cardsBySlug(allCards, $route.params.slug)"
        :key="card.id"
      >
      <div class="content-container">
          <div v-swiper:cardSlide="swiperOptions"
              ref="cardSlide"
              class="swiper-container"
              @slideChangeTransitionEnd="setCardActive"
              @ready="setCardActive"
              >
              <div class="swiper-wrapper">
                <div
                    class="card-item swiper-slide"
                    :data-question="q.question"
                    :data-slug="$route.params.slug"
                    :data-ad="q.advertisement"
                    v-for="q in card.questions" :key="q.id"
                >
                  <!-- Intro -->
                  <intro
                    v-if="q.intro"
                    :card="card"
                    :nextSlide="nextSlide"
                    :cardSlide="cardSlide"
                    :images="images"
                    :background="background"
                  />

                  <!-- Advertisement -->
                  <advertisement
                      v-if="q.advertisement"
                      :ad="q"
                      :nextSlide="nextSlide"
                      :cardSlide="cardSlide"
                  />

                  <!-- Question Card -->
                  <div class="question" v-if="q.question !== ''">
                    <div class="save"
                         v-bind:class="{saved: isSaved}"
                         @click="addActiveCardToFav"
                      >
                      <span>Save Question</span>
                      <svg xmlns="http://www.w3.org/2000/svg"
                           viewBox="0 0 21.599 15.35">
                            <path class="a"
                              d="M17.791,4.355a4.631,4.631,0,0,0-6.55,0l-.892.892-.892-.892a4.632,4.632,0,0,0-6.55,6.55L3.8,11.8l6.55,6.55L16.9,11.8l.892-.892a4.631,4.631,0,0,0,0-6.55Z"
                              transform="translate(-0.8, -2.998)"/>
                      </svg>
                    </div>

                    <div class="question-container">
                      <span class="paragraph-big">{{q.question}}</span>
                      <a class="nextButton heading-4" @click="slideToNext">Next Question</a>
                    </div>
                  </div>

                  <!-- Outro -->
                  <outro
                      v-if="q.outro"
                      :outro="q"
                      :nextSlide="nextSlide"
                      :cardSlide="cardSlide"
                      :images="images"
                      :background="background"
                  />
                </div>
              </div>
          </div>
      </div>
    </div>
    <mainFooter
        :prevSlide="prevSlide"
        :cardSlide="cardSlide"
        :favSlug="favSlug"
        :isStandalone="isStandalone"
        :isAndroid="isAndroid"
        :isIOS="isIOS"
    />
  </div>
</template>

<script>
  import { set, get } from 'idb-keyval'
  import { mapGetters } from 'vuex';
  import {directive } from 'vue-awesome-swiper'
  import 'swiper/swiper-bundle.css'
  import { Swiper, EffectFade } from 'swiper';

  // Components
  import Header from "../partials/Header";
  import Footer from "../partials/Footer";
  import Intro from "../partials/Intro";
  import Outro from "../partials/Outro";
  import Advertisement from "../partials/Advertisment";


  // Assets
  import background from '../../assets/background.svg'
  import arrow from '../../assets/arrow-right-icon.svg'

  Swiper.use([EffectFade]);

  export default {
    components: {
      mainHeader: Header,
      mainFooter: Footer,
      intro: Intro,
      outro: Outro,
      advertisement: Advertisement
    },
    directives: {
      swiper: directive
    },
    props: ['isStandalone'],
    data() {
      return {
        isSaved: false,
        isAd: false,
        prevSlide: null,
        nextSlide: null,
        favSlug: this.$route.params.slug,
        swiperOptions: {
          direction: 'horizontal',
          shortSwipes: false,
          centeredSlides: true,
          effect: 'fade',
          fadeEffect: { crossFade: true },
          allowTouchMove: false
        },
        images: {
          background: background,
          arrow: arrow
        },
        background: {
          background: 'url(' + background + ')',
          backgroundSize: 'contain',
        }
      }
    },
    computed: {
      ...mapGetters({
        allCards: 'allCards',
        allCardsLoaded : 'allCardsLoaded',
        activeCard: 'activeCard',
      }),
    },
    methods: {
      async setCardActive() {
        // set Slide Indexes
        this.prevSlide = (this.cardSlide.activeIndex - 1)
        this.nextSlide = (this.cardSlide.activeIndex + 1)

        // Set Question as active
        let question = document.querySelector('.swiper-slide-active.card-item')

        // Check if card item is an advertisement
        // If so hide header
        if (question.dataset.ad === 'true') {
          this.isAd = true
        } else {
          this.isAd = false
        }

        // Set card as active in store
        this.$store.dispatch('setActiveCard', {
          question: question.dataset.question,
          slug: question.dataset.slug
        });

        // Check if question is saved
        let isQuestionSaved = false
        get('favorites').then((val) => {
          if (val !== undefined) {
            new Promise((resolve) => {
              val.forEach((obj) => {
                if (obj.question === this.activeCard.question) {
                  isQuestionSaved = true
                }
                setTimeout(() => {
                  resolve()
                })
              })
            })
            .then(() => {
              // Set heart icon class
              if (isQuestionSaved) {
                this.isSaved = true
              } else {
                this.isSaved = false
              }
            })
            .catch((error) => {
              console.log(error)
            })
          }
        })
      },
      async addActiveCardToFav() {
        this.isSaved = true

        const currentFavorites = await get('favorites')
        if (currentFavorites === undefined) {
          // Set Favorite for the first time
          var favoritesArray = []
          favoritesArray.push(this.activeCard)
          set('favorites', favoritesArray)
        }
        else {
          // Updated local store
          get('favorites').then((val) => {
            new Promise((resolve) => {
              val.forEach((obj) => {
                // Check for duplicates
                if (obj.question === this.activeCard.question) {
                  this.isDuplicate = true
                }
              })
              setTimeout(() => {
                resolve()
              })
            })
            // Add active card to favorites
            .then(() => {
              if (this.isDuplicate !== true) {
                val.push(this.activeCard)
                set('favorites', val)
              }
            })
            .catch((error) => {
              console.log(error)
            })
          })
        }
      },
      cardsBySlug(cards, slug) {
         return cards.filter(card => card.slug === slug)
      },
      slideToFirst() {
        this.cardSlide.slideTo(1, 500)
      },
      slideToNext() {
        this.cardSlide.slideTo(this.nextSlide, 500)
      }
    },
    mounted() {
      this.$ua.trackView(this.$route.params.slug)

    }
  }
</script>

