import axios from 'axios';
import SETTINGS from '../settings';

export default {
    getCards(cb) {
        axios
            .get(SETTINGS.API_BASE_PATH + 'cards')
            .then(response => {
                cb(response.data);
            })
            .catch(e => {
                cb(e);
            });
    },
    getPage(pageID, cb) {
        axios
            .get(SETTINGS.API_BASE_PATH + 'pages/' + pageID)
            .then(response => {
                cb(response.data);
            })
            .catch(e => {
                cb(e);
            });
    },
};