            <router-link to="/cards/date-night">
              <div class="icon">
                <img :src="images.dateNight" alt="Date Night">
              </div>
              <div class="title">
                <span class="heading-3">Date Night</span>
              </div>
            </router-link>
          </div>

          <!-- Girls Night -->
          <div class="callout-container" v-if="callout.slug === 'girls-night'">
            <router-link to="/cards/girls-night">
              <div class="icon">
                <img :src="images.girlsNight" alt="Girls Night">
              </div>
              <div class="title">
                <span class="heading-3">Girls Night</span>
              </div>
            </router-link>
          </div>

          <!-- Family Friendly -->
          <div class="callout-container" v-if="callout.slug === 'family-friendly'">
            <router-link to="/cards/family-friendly">
              <div class="icon">
                <img :src="images.familyFriendly" alt="Family Friendly">
              </div>
              <div class="title">
                <span class="heading-3">Family Friendly</span>
              </div>
            </router-link>
          </div>
