    </div>
  </div>
</template>

<script>
// Assets
import arrow from "../../assets/arrow-left-dark-icon.svg";
import dateNight from "../../assets/date-night-icon.svg";
import girlsNight from "../../assets/girls-night-icon.svg";
import familyFriendly from "../../assets/family-friendly-icon.svg";


export default {
  props: [
    'outro',
  ],
  data() {
    return {
      images: {
        arrow: arrow,
        dateNight: dateNight,
        girlsNight: girlsNight,
        familyFriendly: familyFriendly
      }
    }
  },
}
</script>

<style lang="scss">
@import '../../scss/colors';
@import '../../scss/text';
@import '../../scss/variables';

.question-outro {
  width: 100%;
  height: calc(100% - 180px) !important;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .outro-container {
    position: relative;
    top: -20px;
    max-width: 327px;
    border-radius: 0 80px 80px 80px;
    box-shadow: 0pt 3pt 25pt $yellow;
    padding: 60px 40px;
    margin: 0 20px;

    @media screen and (max-width: 374px) {
      padding: 20px;
    }

    @supports (-webkit-touch-callout: none) {
      /* CSS specific to iOS devices */
      top: -100px !important;
    }

    .title {
      padding-bottom: 10px;
    }

    p {
      margin: 0;
    }
  }

  .callouts {
    display: flex;

    .callout {
      margin: 20px 0;

      .callout-container {
        a {
          text-decoration: none;
        }

