          flex-direction: column;
          justify-content: center;
          align-items: center;
          width: 70px;
          height: 70px;
          border: 2px solid $black;
          border-radius: 50%;
          margin: 10px 20px;

          @media screen and (max-width: 374px) {
            margin: 10px;
          }

          img {
            width: 22px;
          }
        }
        .title {
          margin-top: 5px;
          span {
            color: $black;
          }
        }

      }
    }
  }

  .callout-link {
    text-align: center;
    a {
      display: flex;
      align-items: center;
      color: $black;
      text-decoration: none;
      font-weight: 500 !important;

