import * as types from '../mutation-types';
import api from "../../api";

// initial state
const state = {
    page: [],
};

// getters
const getters = {
    pageContent: state => state.page,
};

// actions
const actions = {
    async getPage({ commit }, pageID) {
        api.getPage(pageID, page => {
            commit(types.FETCH_PAGE, { page })
        });
    },
};

// mutations
const mutations = {
    [types.FETCH_PAGE](state, page ) {
        state.page = page
    },
};

export default {
    state,
    getters,
    actions,
    mutations,
};