<template>
  <div class="footer-home-container">
    <div v-if="isStandalone === false" class="install">
      <span @click="installApp" class="install">Save To Homescreen</span>
    </div>
    <div class="copyright">
      <span>&copy;{{ getYear() }} THE WINE GROUP | <router-link to="/page/39">TERMS</router-link> | <router-link to="/page/3">PRIVACY</router-link></span>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import { set, get } from 'idb-keyval'
import Heart from '../../assets/heart-red.svg';

export default {
  data() {
    return {
      favoriteAdded: false,
      favoriteDuplicate: false,
      isDuplicate: false,
      deferredPrompt: null,
      installable: true,
      images: {
        heart: Heart,
      },
    }
  },
  props: ['isStandalone', 'isAndroid', 'isIOS'],
  computed: {
    ...mapGetters({
      activeCard: 'activeCard',
      allFavorites: 'allFavorites',
      openBanner: 'openBanner'
    }),
    routeName() {
      return this.$router.currentRoute.name
    }
  },
  methods: {
    getYear() {
      var date = new Date();
      var dateYear = date.getFullYear();
      return dateYear
    },
    async addActiveCardToFav() {
      const currentFavorites = await get('favorites')

      if (currentFavorites === undefined) {
        // Set Favorite for the first time
        var favoritesArray = []
        favoritesArray.push(this.activeCard)
        set('favorites', favoritesArray)
      }
      else {
        // Updated local store
        get('favorites').then((val) => {
          new Promise((resolve) => {
            val.forEach((obj) => {
              if (obj.question === this.activeCard.question) {
                this.isDuplicate = true
              }
            })
            setTimeout(() => {
              resolve()
            })
          })
              .then(() => {
                if (this.isDuplicate !== true) {
                  val.push(this.activeCard)
                  set('favorites', val)
                }
              })
              .catch((error) => {
                console.log(error)
              })
        })
      }
    },
    checkDeferredPrompt() {
      window.addEventListener('beforeinstallprompt', (e) => {
        e.preventDefault()
        this.deferredPrompt = e
        console.log('beforeinstallprompt event was fired')
      })
    },
    async installApp() {
      //Install prompt only if desktop
      if (!this.isAndroid && !this.isIOS) {
        this.deferredPrompt.prompt()
        const {outcome} = await this.deferredPrompt.userChoice;

        if (outcome !== 'dismissed') {
          this.installable = false
          this.deferredPrompt = null
        }
      }
      if (this.isStandalone === false) {
        this.$store.dispatch('setOpenBannerStatus', true)
      }
    },
  },
  mounted() {
    this.checkDeferredPrompt()
  },
}
</script>

<style lang="scss">
@import '../../scss/colors';
@import '../../scss/text';
@import '../../scss/variables';

.footer-home-container {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: $footerHeight;
  display: flex;
  flex-direction: column;
  background: linear-gradient(180deg, rgba(255,255,255, 0.3) 0%, rgba(245,241,218,0.8049194677871149) 63%, rgba(232,223,171,0.7337710084033614) 100%);

  .install {
    position: relative;
    top: 25%;
    span {
      display: inline-block;
      padding: 10px 15px;
      font-family: nimbus-sans, sans-serif;
      font-weight: 700;
      font-style: normal;
      letter-spacing: 0.2px;
      font-size: 13px;
      line-height: 1;
      border: 1px solid $black;
      border-radius: 9000px;
      cursor: pointer;
      transition: all .25s ease;

      &:hover {
        background: $black;
        color: $white;
      }
    }
  }

  .favorites {
    display: flex;
    flex-direction: column;
    position: fixed;
    right: 40px;
    bottom: 30px;

    img {
      align-self: center;
    }
    span {
      display: inline-block;
      cursor: pointer;
      font-family: nimbus-sans, sans-serif;
      font-weight: 700;
      font-style: normal;
      letter-spacing: 0.2px;
      font-size: 12px;
      line-height: 1;
      color: $black;
      margin-top: 5px;
    }
  }

  .copyright {
    position: fixed;
    bottom: 5px;
    width: 100%;

    span {
      font-family: nimbus-sans, sans-serif;
      font-weight: 500;
      font-style: normal;
      font-size: 9px;
      line-height: 1;
      color: $grey;

      a {
        text-decoration: none;
        color: $grey;
      }
    }
  }
}
</style>