import api from '../../api';
import * as types from '../mutation-types';

// initial state
const state = {
    all: [],
    loaded: false,
    active: {
        question: '',
        slug: ''
    }
};

// getters
const getters = {
    allCards: state => state.all,
    allCardsLoaded: state => state.loaded,
    activeCard: state => state.active,
};

// actions
const actions = {
    getCards({ commit }) {
        api.getCards(cards => {
            commit(types.FETCH_CARDS, { cards })
            commit(types.CARDS_LOADED, true);
        });
    },
    setActiveCard({ commit }, active) {
        commit(types.ACTIVE_CARD, active)
    }
};

// mutations
const mutations = {
    [types.FETCH_CARDS](state, { cards }) {
        state.all = cards;
    },
    [types.CARDS_LOADED](state, val) {
        state.loaded = val;
    },
    [types.ACTIVE_CARD](state, active) {
        state.active = active;
    },
};

export default {
    state,
    getters,
    actions,
    mutations,
};